'use client'

import { useState, useMemo } from 'react'
import Image from 'next/image'
import members from '@/data/members.json'
import type { Member } from '@/types'
import SectionHeader from '@/components/ui/SectionHeader'

const membersData: Member[] = members
const activeMembers = membersData.filter((member) => !member.isExec && !member.isAlumni)

// Get unique pledge classes
const pledgeClasses = [...new Set(activeMembers.map((m) => m.pledgeClass).filter(Boolean))] as string[]

export default function ActiveMembers() {
  const [selectedPledgeClass, setSelectedPledgeClass] = useState<string>('all')

  const filteredMembers = useMemo(() => {
    if (selectedPledgeClass === 'all') return activeMembers
    return activeMembers.filter((member) => member.pledgeClass === selectedPledgeClass)
  }, [selectedPledgeClass])

  return (
    <section className="py-16 md:py-24 bg-primary-light/30">
      <div className="max-w-7xl mx-auto px-6">
        <SectionHeader
          title="Active Members"
          subtitle="The brothers who embody our values every day"
        />

        {/* Filter */}
        <div className="mt-12 flex flex-wrap justify-center gap-2">
          <button
            onClick={() => setSelectedPledgeClass('all')}
            className={`px-4 py-2 rounded-full text-sm font-medium transition-all ${
              selectedPledgeClass === 'all'
                ? 'bg-accent text-white'
                : 'bg-primary-light border border-secondary/10 text-secondary/60 hover:border-secondary/20 hover:text-secondary'
            }`}
          >
            All Members
          </button>
          {pledgeClasses.sort().reverse().map((pledgeClass) => (
            <button
              key={pledgeClass}
              onClick={() => setSelectedPledgeClass(pledgeClass)}
              className={`px-4 py-2 rounded-full text-sm font-medium transition-all ${
                selectedPledgeClass === pledgeClass
                  ? 'bg-accent text-white'
                  : 'bg-primary-light border border-secondary/10 text-secondary/60 hover:border-secondary/20 hover:text-secondary'
              }`}
            >
              {pledgeClass}
            </button>
          ))}
        </div>

        {/* Members Grid */}
        <div className="mt-12 grid grid-cols-3 sm:grid-cols-4 md:grid-cols-5 lg:grid-cols-6 gap-4">
          {filteredMembers.map((member) => (
            <div key={member.id} className="group">
              <div className="relative aspect-square overflow-hidden rounded-xl bg-primary-light border border-secondary/10 transition-all duration-300 group-hover:border-accent/30">
                <Image
                  src={member.photo}
                  alt={member.name}
                  fill
                  className="object-cover grayscale group-hover:grayscale-0 transition-all duration-500 group-hover:scale-110"
                  unoptimized
                />
                <div className="absolute inset-0 bg-gradient-to-t from-primary/80 to-transparent opacity-0 group-hover:opacity-100 transition-opacity" />

                {/* Info on hover */}
                <div className="absolute bottom-0 left-0 right-0 p-2 translate-y-full group-hover:translate-y-0 transition-transform duration-300">
                  <p className="text-white text-xs font-medium truncate">{member.name}</p>
                  <p className="text-white/60 text-xs truncate">{member.major}</p>
                </div>
              </div>
              <p className="mt-2 text-secondary/70 text-xs text-center truncate">
                {member.name}
              </p>
            </div>
          ))}
        </div>

        {filteredMembers.length === 0 && (
          <p className="mt-12 text-center text-secondary/50">
            No members found for the selected filter.
          </p>
        )}
      </div>
    </section>
  )
}
